import Vue from 'vue';
import Buefy from 'buefy';
import '../scss/main.scss';
import Components from './components/all';
import mountApplication from './scripts/mountApplication';

Vue.component('b-tabs', Buefy.Tabs);
Vue.component('b-tab-item', Buefy.TabItem);

Object.entries(Components).forEach(entry => {
    Vue.component(entry[0], entry[1]);
});

document.addEventListener("DOMContentLoaded", function () {
    mountApplication();
    document.getElementById('copyButton').addEventListener('click', function () {
        copyToClipboard('activecampaign_status');
    });

    function copyToClipboard(element_id) {
        let aux = document.createElement("div");
        aux.setAttribute("contentEditable", true);
        aux.innerHTML = document.getElementById(element_id).innerHTML;
        aux.setAttribute("onfocus", "document.execCommand('selectAll',false,null)");
        document.body.prepend(aux);
        aux.focus();
        document.execCommand("copy");
        document.body.removeChild(aux);
        document.getElementById('copyStatus').innerHTML = 'Copied!';
        setTimeout(() => {
            document.getElementById('copyStatus').innerHTML = '';
        }, 5000);
    }

});



